/* Add here all your CSS customizations */
.panel-title.form {
  padding-left: 15px;
  margin: 5px 0 5px 0;
}

.form-group.reset-form-horizontal {
  margin-right: 0;
  margin-left: 0;
}

.list-items .body-sign .panel-sign .panel-body {
  background: #FFF;
  border-radius: 5px 0 5px 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 33px 19px 15px;
}

.list-items .body-sign .panel-sign .panel-body .buttons .col-xs-5:first-child {
  margin-left: -14px;
  margin-right: 9px;
}

.list-items #filters {
  margin: 0 0 10px 0;
}

.list-items #search {
  float: left;
}

.list-items .mb-md {
  margin-bottom: 5px !important;
}

.list-items .mb-md .input-sm {
  width: 86%;
  margin: 0 0 0 10px;
  display: inline-block;
}

.list-items .btn-success {
  margin: 0 0 0 10px;
}

.list-items .body-locked {
  max-width: none;
  min-height: 312px;
  height: 312px;
  background: none;
  width: 170px;
  float: left;
}

.list-items .body-locked .panel-sign {
  margin: 0 auto;
  max-width: 170px;
  padding-top: 40px;
}

.list-items .body-locked .current-user {
  margin-top: 14px;
  margin-bottom: 35px;
  width: 98px;
}

.list-items .body-locked .current-user .user-image {
  border: 5px solid #0088cc;
  border-radius: 150px;
  height: 90px;
  left: 73%;
  position: absolute;
  margin-left: -81px;
  top: -52px;
  width: 90px;
}

.list-items .body-locked .current-user .user-name {
  font-size: 30px;
  font-size: 1.5rem;
  line-height: 36px;
  line-height: 3.6rem;
}

.list-items .multipleselect {
  width: 100%;
}

.list-items .controlsMultipleSelect {
  width: 100%;
}

.list-items .inner-toolbar {
  margin: 0 0 10px 0 !important;
  position: static !important;
}

.list-items .content-with-menu {
  border-top: 110px solid transparent;
  margin: -107px -35px -46px 16px;
  min-height: 100vh;
  width: 100%;
}

.list-items .checkbox-custom label {
  font-size: 9px;
}

.list-items .media-gallery a {
  font-size: 9px;
}

.list-items .btn-primary {
  font-size: 13px;
}

.list-items .form-horizontal .control-label {
  text-align: left;
}

.list-items .col-md-2 {
  width: 13.66666667%;
}

#albums_photos_add .form-horizontal .control-label {
  text-align: left;
}

#albums_photos_add #thumbails_images img {
  max-width: 100%;
  max-height: 158px;
  width: 386px;
  height: 151px;
}

#albums_photos_add #thumbails_images input {
  border-radius: 0px;
}

#add-review .content-result {
  display: inline;
}

#add-review .separate_icon {
  margin: 0 0 0 25px;
}

#add-review .equal_sign_diary {
  font-size: 20px;
  font-weight: bold;
}

#albums_photos_add .fileupload .uneditable-input, #profile .fileupload .uneditable-input {
  display: none;
}

#profile .fileupload .btn-file {
  margin: 0 0 0 -14px;
}

.post-image-holder a {
  width: 32.5%;
  max-height: 100px;
  margin-bottom: 10px;
  display: inline-block;
  background-size: cover !important;
}

.post-image-holder.single-image a img {
  max-height: inherit !important;
  height: auto;
  width: 100%;
}

.timeline .panel-heading {
  padding: 14px 15px;
}

.timeline .post-author .post-options {
  float: right;
  color: #859AB5;
  margin-top: 6px;
}

.timeline .post-author .post-options .dropdown-menu {
  right: -2px;
  left: auto;
}

.timeline .post-author .user-avatar {
  width: 40px;
  height: 40px;
  float: left;
  margin-right: 10px;
}

.timeline .post-author .user-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.timeline .socialite {
  background-color: #fff;
  border-top: 0px;
  padding: 15px;
  border: 1px solid #E6EAEE;
  border-top-width: 0px;
}

.timeline .panel-body {
  border: 1px solid #E6EAEE;
  border-top-width: 0px;
  padding-bottom: 7px;
}

.timeline .text-wrapper .post-image-holder {
  margin-top: 10px;
  max-height: 200px;
  overflow: hidden;
}

.timeline .text-wrapper .post-image-holder img {
  max-width: 100%;
  max-height: 200px;
  margin-top: 3px;
  margin-right: 3px;
  margin-bottom: 3px;
  border-radius: 4px;
}

.timeline .text-wrapper .post-image-holder.single-image {
  max-height: inherit !important;
  height: auto;
}

.timeline .text-wrapper .post-image-holder.single-image a {
  max-height: inherit;
  height: auto;
  width: 100%;
}

.timeline .panel-footer.socialite {
  margin: 0;
  background-color: #fff;
  border-top: 0px;
  padding: 15px;
  border: 1px solid #E6EAEE;
  border-top-width: 0px;
}

.timeline .comments-section {
  width: 100%;
  height: auto;
  background-color: #eff3f6;
}

.timeline .comments-section .comments-wrapper {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 26px;
  height: auto;
  background: #FCFCFC;
  border: 1px solid #E6EAEE;
  border-top-width: 0px;
}

.timeline .comments-section .comments-wrapper .comment-form {
  position: relative;
}

.timeline .comments-section .comments-wrapper .comment-form .meme-reply {
  position: absolute;
  top: 50%;
  margin-bottom: 0px;
  margin-top: -9px;
  right: 10px;
}

.timeline .comments-section .comments-wrapper .comment-form .meme-reply li a i {
  font-size: 16px;
  color: #859AB5;
}

.timeline .comments-section .comments-wrapper .to-comment {
  padding-top: 16px;
}

.timeline .comments-section .comments-wrapper .to-comment.comment-reply {
  margin-left: 45px;
  padding-top: 12px;
}

.timeline .comments-section .comments-wrapper .to-comment.comment-reply .commenter-avatar {
  width: 30px;
  height: 30px;
}

.timeline .comments-section .comments-wrapper .to-comment.comment-reply .comment-textfield {
  padding-left: 45px;
}

.timeline .comments-section .comments-wrapper .to-comment.comment-reply .comment-textfield .form-control {
  height: 30px;
  font-size: 13px;
}

.timeline .comments-section .comments-wrapper .commenter {
  margin-top: -3px;
  padding-left: 45px;
}

.timeline .comments-section .comments-wrapper .commenter-avatar {
  width: 36px;
  height: 36px;
  float: left;
  display: inline-block;
}

.timeline .comments-section .comments-wrapper .commenter-avatar img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}

.timeline .comments-section .comments-wrapper .comment-replies {
  margin-left: 45px;
}

.timeline .comments-section .comments-wrapper .comment-textfield {
  padding-left: 51px;
}

.timeline .comments-section .comments-wrapper .comment-textfield .form-control {
  border-radius: 0px;
  padding-top: 8px;
  padding-right: 60px;
  padding-bottom: 8px;
}

.timeline .comments-section .comments-wrapper .comments {
  padding-top: 15px;
}

.timeline .comments-section .comments-wrapper .delete-comment {
  float: right;
  margin-right: 2px;
  font-size: 14px;
  color: #7F8FA4;
}

.timeline .comments-section .comments-wrapper .commenter-name a {
  font-weight: 600;
  font-size: 14px;
  color: #2298F1;
  letter-spacing: 0px;
  text-transform: capitalize;
}

.timeline .comments-section .comments-wrapper .commenter-name .comment-description {
  font-weight: 400;
  font-size: 13px;
  color: #5B6B81;
  margin-left: 8px;
}

.timeline .comments-section .comments-wrapper .comment-options {
  margin-top: 1px;
}

.timeline .comments-section .comments-wrapper .comment-options li {
  padding-left: 4px;
  padding-right: 4px;
  color: #859AB5;
}

.timeline .comments-section .comments-wrapper .comment-options li:first-child {
  padding-left: 5px;
}

.timeline .comments-section .comments-wrapper .comment-options li a {
  font-weight: 400;
  font-size: 12px;
  color: #859AB5;
  text-transform: capitalize;
}

.timeline .comments-section .comments-wrapper .comment-options li a i {
  margin-right: 3px;
}

.timeline .comments-section .comments-wrapper .replies-count {
  margin-top: 13px;
  width: 100px;
  display: block;
  text-transform: capitalize;
  font-weight: 400;
  font-size: 13px;
  color: #9FA9BA;
  margin-left: 45px;
}

.timeline .comments-section .comments-wrapper .replies-count i {
  font-size: 16px;
  color: #9FA9BA;
  margin-right: 5px;
  vertical-align: -1px;
}

#color-chooser {
  list-style: none;
  padding-left: 0;
}

#color-chooser li {
  float: left;
  margin-right: 5px;
}

#color-chooser .text-yellow {
  color: yellow;
}

#color-chooser .text-orange {
  color: orange;
}

#color-chooser .text-red {
  color: red;
}

#content-btn-add-event .input-group-btn #add-new-event {
  margin: 10px 0 0 15px;
}

@media (min-width: 768px) {
  html.fixed .content-body {
    margin-left: 0;
  }
  html.fixed .page-header {
    left: 0;
  }
  .content-body {
    padding: 20px;
  }
  #new-event {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }
  #content-btn-add-event {
    display: -webkit-inline-box;
    margin: 18px 0 0 0;
  }
  #content-btn-add-event button {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
}
